nav{
    width: 100%;
    background-color: #123;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .nav-links{
        list-style: none;
        display: flex;

        .nav-item a{
            display: inline-block;
            text-decoration: none;
            color: white;
            padding: 1em 2em;

            &:hover{
                color: #abc;
            }
            &.active{
                font-weight: bold;
            }
        }
    }
}